.header {
  position: fixed;
  box-shadow: rgba(211, 211, 211, 0.75) -1px 0px 5px;
  width: 100%;
  background: white;
  z-index: 5;

  &__content {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;
    align-items: center;
    width: 49%;
    padding: 0 0 0 1rem;
  }

