  //       }
  //     }

  //     a {
  //       display: flex;
  //       // padding: 2.9rem 1rem;
  //       padding: 4rem 2rem;
  //       transition: all 0.2s ease-in-out;
  //     }
  //   }
  // }

  &__nav {
    display: flex;
    width: 51%;

    &--li {
      display: flex;
      justify-content: center;
      align-items: center;
      list-style-type: none;

      &:nth-of-type(1) {
        flex: 1;
      }

      &:nth-of-type(2) {
        flex: 1;
      }

      &:nth-of-type(3) {
        flex: 2;
      }

      &:nth-of-type(4) {
        flex: 2;
      }

      &:nth-of-type(5) {
        flex: 2;
      }

      &:nth-of-type(6) {
        flex: 1;
      }

      &:nth-of-type(7) {
        flex: 1;
      }
      &:hover {
        background: $color-primary3;

        a {
          color: white;
        }
      }

      a {
        padding: 3rem 0;
      }
    }
  }
}
